:root {
  --background-light: rgb(246, 245, 245);
  --text-color1: #838383;
  --text-color2: seagreen;
  --background-dark: rgb(50, 50, 50);
}

html {
  font-family: 'Raleway', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
  font-size: 10px;

  background: var(--background-light);
  color: rgba(28, 28, 28, 0.95);
}

.content {
  min-height: calc(100vh - 2.5rem);
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none;   /* Chrome/Safari/Opera */
  -moz-user-select: none;      /* Firefox */
  -ms-user-select: none;       /* Internet Explorer/Edge */
  user-select: none;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(#838383, var(--background-light));
}

h1 {
  margin: 0;
  padding: 2rem 0.5rem 2rem 2rem;
  font-size: 3rem;
}

header div {
  display: grid;
  justify-items: end;
  gap: 1rem;
  padding-right: 1rem;
  font-size: 1rem;
}

.date span{
  float: right;
}

.language{
  cursor: pointer;
  font-size: 1.2rem;
  border-bottom: solid 1px rgba(255, 255, 255, 0);
}

.language:hover, .language:focus {
  color: var(--text-color2);
  border-bottom: solid 1px var(--text-color2) ;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--background-dark);
  height: 3rem;
  gap: 1rem;
}

.form{
  flex-basis: content;
}
.search-input{
  margin-left: 2rem;
  padding: 0.2rem 0;
  background-color: var(--background-dark);
  font-size: 1rem;
  color: var(--background-light);
  width: 16rem;
  outline:none;
  border-radius: 0.1rem;
  border: var(--background-dark) solid;
  font-weight: normal;
  font-family: 'Raleway', sans-serif;
}

.search-input:hover, .search-input:focus {
  box-shadow: 0 0 0.1rem 0 var(--text-color1);
  -webkit-box-shadow: 0 0 0.1rem 0 var(--text-color1);
  -moz-box-shadow: 0 0 0.1rem 0 var(--text-color1);
}

.search-form {
  position: relative;
}

.search-form div{
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 1.9rem;
  cursor: pointer;
}

.img {
  height: 1.2rem;
  margin: auto;
}

.img g{
  fill: var(--text-color1);
}

.search-form div .img:hover > g {
  fill: var(--background-light);
}

.dropdown {
  position: absolute;
  width: 19rem;
  max-height: 20rem;
  padding: 0;
  margin: 0.8rem 0;
  overflow: auto;
  list-style: none;
  color: var(--background-light);
  background-color: rgba(25, 25, 25, 0.9);
  font-size: 1rem;
  z-index: 2;
}

.dropdown::-webkit-scrollbar {
  width: 0.5rem;
  background-color: rgba(0, 0, 0, 0);
}

.dropdown::-webkit-scrollbar-thumb {
  background-color: var(--text-color2);
}

.dropdown li {
  padding: 0.5rem 0 0.5rem 2rem;
}

.dropdown li:hover, .dropdown li:focus{
  background-color: var(--text-color2);
  cursor: pointer;
  transition: cubic-bezier(1, 0, 0, 1) .3s all;
}

.show-currencies-mobile{
  padding: 0.5rem 1rem 0.5rem 0.5rem;
  font-size: 1rem;
  color: var(--background-light);
}

.currencies-block {
  background-color: var(--background-dark);
  color: var(--background-light);
}

.currencies-block-pc {
  display: flex;
  align-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
  overflow: hidden;
  height: 3rem;
}

.currencies-block-pc:hover, .currencies-block-pc:focus {
  align-self: baseline;
  overflow: visible;
  height: auto;
  z-index: 2;
}

.currencies-block div {
  min-width: 3rem;
  padding: 0.9rem;
  text-align: center;
  font-size: 1rem;
  cursor: pointer;
}

.currencies-block-mobile {
  display: none;
  width: 100%;
  grid-template-columns: repeat(auto-fill, minmax(4.8rem, 1fr));
  height: auto;
}

.currencies-block div:hover, .currencies-block div:focus{
  background-color: var(--text-color2);
}

.currency-block {
  position: relative;
  display: inline-block;
}

.tooltip {
  font-size: 0.7rem;
  overflow-wrap: anywhere;
}

.currency-block .tooltip {
  position: absolute;
  bottom: 100%;
  left: 0;
  padding: 0.1rem;
  min-width: calc(100% - 0.2rem);
  background-color: var(--text-color2);
  color: var(--background-light);
  text-align: start;

  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s;
}

.currency-block:hover .tooltip, .currency-block:focus .tooltip {
  visibility: visible;
  opacity: 1;
}

.loader {
  margin: 2rem;
}

.error {
  padding-left: 2rem;
  font-size: 2rem;
  color: red;
}

.inputs {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(10rem, 1fr));
  gap: 1rem;
  padding: 2rem;
}

.input-item form label {
  font-size: 0.8rem;
}

.input-item label{
  display: inline-block;
  padding: 0.2rem;
  font-size: 1rem;
  color: var(--text-color1);
}


.delete {
  margin-left: 4rem;
  padding: 0.1rem 1rem;
  position: absolute;
  font-size: 0.8rem;
  cursor: pointer;

  opacity: 0;
  transition: opacity 0.5s;
}

.input-item:hover .delete, .delete:focus {
  visibility: visible;
  opacity: 1;
}

.delete:hover, .delete:focus {
  color: red;
}

.input-value {
  display: block;
  max-width: 10rem;
  height: 2rem;
  margin-bottom: 1rem;

  outline:none;
  border: none;
  background: transparent;

  font-family: 'Raleway', sans-serif;
  font-size: 1.5rem;
}

footer {
  padding: 0 2rem;
}

footer a:hover, footer a:focus{
  color: var(--text-color2);
}

footer a:visited, footer a:link {
  color: var(--text-color1);
}

@media screen and (min-width: 1240px) {
  .show-currencies-mobile {
    display: none;
  }
}

@media screen and (max-width: 1240px) {
  .nav {
    padding-right: 0.5rem;
  }
  .show-currencies-mobile {
    display: block;
  }

  .currencies-block-pc, .tooltip {
    display: none;
  }
}

@media screen and (max-width: 900px) {
  h1{
    font-size: 2.5rem;
  }

  .inputs {
    padding: 1rem 1rem 1rem 2rem;
    gap: 0.5rem;
  }
}

@media screen and (max-width: 470px) {
  html {
    font-size: 14px;
  }

  header div {
    display: grid;
    justify-items: end;
    gap: 0.2rem;
    padding-right:0.5rem;
    font-size: 0.7rem;
  }

  h1{
    padding: 1rem;
    font-size: 2rem;
  }

  .language{
    font-size: 1rem;
  }

  .search-input {
    width: 12rem;
    margin-left: 1rem;
  }

  .dropdown{
    width: 100%
  }

  .dropdown li {
    padding-left: 1rem;
  }

  .show-currencies-mobile{
    padding: 0.5rem;
  }

  .loader {
    margin: 1rem;
  }

  .inputs {
    padding: 1rem;
    gap: 0.5rem;
  }

  footer {
    padding: 0 1rem;
  }
}





